.goodsGrid {
  display: grid;
  margin-top: 128px;
}
.goodsGrid1 {
  font-size: 32px;
  display: grid;
  grid-template-columns: auto auto auto;
  width: 100%;
  padding-left: 12px;
}
.image_goods {
  display: flex;
  width: 31.5vw;
  max-height: 520px;
  border-radius: 10px;
}
.goodsGrid_blur {
  filter: blur(8px);
}
.goods_up {
  text-align: center;
  justify-content: center;
  display: grid;
  grid-template-columns: auto;
  margin: 6px;
  object-fit: cover;
}
.goodstext {
  font-size: 1.4vw;
  margin-top: 16px;
  margin-bottom: 22px;
}
@media (max-width: 1025px) {
  .goodstext {
    font-size: 2vw;
  }
  .image_goods {
    width: 31vw;
  }
}
@media (max-width: 769px) {
  .goodstext {
    font-size: 3vw;
  }
  .goodsGrid1 {
    grid-template-columns: auto auto;
  }
  .image_goods {
    width: 46vw;
  }
}
@media (max-width: 545px) {
  .goodstext {
    font-size: 3vw;
  }
}
@media (max-width: 426px) {
  .goodstext {
    font-size: 4vw;
  }
}
@media (max-width: 426px) {
  .goodsGrid1 {
    grid-template-columns: auto;
  }
  .image_goods {
    width: 86vw;
  }
}
